.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background-color: white;
  z-index: var(--z-fixed);

}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  height: var(--header-height);
  margin: 0px auto;
  max-width: 1408px;
  padding: 0px 15px;
}

.nav__logo {
  color: var(--first-color);
  transition: color 0.4s;
  font-size: var(--h2-font-size);
  font-family: var(--second-font);
}

.nav__toggle,
.nav__close {
  font-size: 1.5rem;
  color: var(--title-color);
  cursor: pointer;
}

.nav__cta {
  background-color: var(--first-color);
  color: var(--title-color);
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  padding: 0.75rem 1.5rem;
}

.nav__list {
  display: flex;
  flex-direction: column;
  row-gap: 2.5rem;

  @media screen and (max-width: 1150px) {
    row-gap: 15px;




  }
}

.nav__submenu {
  position: absolute;


  width: 200px;
  padding: 10px;
  border: 2px solid var(--fifth-color);
  background: white;
  box-shadow: 0px 3px 15px rgba(0, 6, 91, 0.1);
  z-index: 99;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;

  a {


    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    text-transform: uppercase;
    color: var(--fifth-color);


  }
}


.nav__submenu-item {
  position: relative;
  display: block;

  a {
    position: relative;
    display: inline-flex;

    &:hover,
    &.active {


      @media screen and (max-width:1150px) {

        color: var(--conversion-first-color);


      }
    }


    @media screen and (min-width: 1150px) {

      &:hover,
      &.active {



        &:before {

          content: "";
          width: 100%;
          height: 4px;
          background: var(--conversion-first-color);
          position: absolute;
          bottom: -3px;
          left: 50%;
          transform: translateX(-50%);



        }




      }
    }
  }
}

@media screen and (min-width: 1150px) {

  .nav-submenu-link:hover>ul.nav__submenu {

    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    filter: alpha(opacity=100);
    opacity: 1;
    visibility: visible;




  }

}



@media screen and (max-width: 1150px) {



  .nav__submenu {

    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    filter: alpha(opacity=100);
    opacity: 1;
    visibility: visible;
    position: relative;

    background: transparent;
    bordeR: none;
    padding: 0px;
    box-shadow: none;


  }


}











.nav__link {
  color: var(--fifth-color);
  font-weight: var(--font-semi-bold);
  transition: color 0.4s;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  text-transform: uppercase;
  display: flex;
  gap: 5px;
  align-items: center;
  position: relative;



  &:hover,
  &.active {
    @media screen and (max-width:1150px) {

      color: var(--conversion-first-color);


    }

    &:before {

      content: "";
      width: 100%;
      height: 4px;
      background: var(--conversion-first-color);
      position: absolute;
      bottom: -3px;
      left: 50%;
      transform: translateX(-50%);

      @media screen and (max-width:1150px) {

        content: none;


      }

    }




  }



}



.nav__close {
  position: absolute;
  top: 1rem;
  right: 1.5rem;
}

.show-menu {
  right: 0 !important;
}

@media screen and (max-width: 1150px) {

  .nav__menu {
    position: fixed;
    top: 0;
    right: -100%;
    background-color: hsla(255, 255, 255, 0.2);
    backdrop-filter: blur(16px);
    -webkit-backdrop-filter: blur(16px);
    width: 80%;
    height: 100%;
    padding: 6rem 3rem 0;
    transition: right 0.4s;
  }
}

@media screen and (min-width: 1150px) {
  .nav {
    height: var(--header-height);
  }

  .nav__toggle,
  .nav__close {
    display: none;
  }

  .nav__list {
    flex-direction: row;
    column-gap: 32px;
  }
}